import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import database from "./database";
import permissions from "./permissions";
import users from "./users.js";
import user from "./user.js";

////////////////////////////////////////////////////////
//-----                  CHATS                   -----//
////////////////////////////////////////////////////////

export default {
    namespaced: true,
    modules: {
        user,
        users,
        database,
        permissions
    },
    state: {
        chats: {},
        chatsToShowIDs: [],

        chatListListener: null,
        chatMessagesListeners: [],

        usernameFilter: null
    },
    getters: {
        getChatByID: (state) => (id) => {
            return state.chats[id];
        },
        getChatsToShowIDs: (state) => {
            return state.chatsToShowIDs;
        },
        getChatMessagesByChatID: (state) => (chatID) => {
            if (state.chats[chatID] != undefined) {
                return state.chats[chatID].messages;
            } else {
                return [];
            }
        },
        getUnreadMessagesByChatID: (state, getters) => (chatID) => {
            let i;
            let myID = getters['user/getID'];
            let unreadMessages = [];

            if (state.chats[chatID] != undefined) {
                for (i = state.chats[chatID].messages.length - 1; i >= 0; i--) {
                    if (state.chats[chatID].messages[i].sender != myID && !state.chats[chatID].messages[i].read) {
                        unreadMessages.push(state.chats[chatID].messages[i].id);
                    } else if (state.chats[chatID].messages[i].sender != myID && state.chats[chatID].messages[i].read) {
                        break;
                    } else if (state.chats[chatID].messages[i].sender == myID) {
                        break;
                    }
                }
            }

            return unreadMessages;
        },
        getUnreadMessages: (state, getters) => {
            let count = 0;

            state.chatsToShowIDs.forEach(c => {
                count += getters.getUnreadMessagesByChatID(c).length;
            });

            return count;
        },
        getChatsToShow: (state, getters) => {
            let chatsToShow = [];

            if (state.usernameFilter == null) {
                state.chatsToShowIDs.forEach((chatID) => {
                    let auxChat = {};
                    auxChat.users = state.chats[chatID].users;
                    auxChat.id = state.chats[chatID].id;
                    auxChat.lastMessage = state.chats[chatID].messages[state.chats[chatID].messages.length - 1];
                    chatsToShow.push(auxChat)
                });
            } else {
                state.chatsToShowIDs.forEach((chatID) => {
                    let auxChat = {};
                    auxChat.users = state.chats[chatID].users;

                    let lowNotMe = getters.getUserByChatID(chatID).username.toLowerCase();

                    if (lowNotMe.includes(state.usernameFilter.toLowerCase())) {
                        auxChat.id = state.chats[chatID].id;
                        auxChat.lastMessage = state.chats[chatID].messages[state.chats[chatID].messages.length - 1];
                        chatsToShow.push(auxChat)
                    }
                });
            }

            return chatsToShow;
        },
        getUserByChatID: (state, getters) => (chatID) => {
            let userID;
            if (state.chats[chatID].users[0] == getters['user/getID']) {
                userID = state.chats[chatID].users[1];
            } else {
                userID = state.chats[chatID].users[0];
            }

            return getters['users/getUserByID'](userID);
        },

        getNotReadMessagesCountByChatID: (state, getters) => (id) => {
            let i, count = 0;
            let myID = getters['user/getID'];

            for (i = state.chats[id].messages.length - 1; i >= 0; i--) {
                if (state.chats[id].messages[i].sender != myID && !state.chats[id].messages[i].read) {
                    count++;
                } else if (state.chats[id].messages[i].sender != myID && state.chats[id].messages[i].read) {
                    break;
                } else if (state.chats[id].messages[i].sender == myID) {
                    break;
                }
            }

            return count;
        }


    },
    mutations: {
        resetState(state) {
            state.chats = {};
            state.chatsToShowIDs.splice(0, state.chatsToShowIDs.length);

            if (state.chatListListener != null && state.chatListListener != undefined) {
                state.chatListListener();
                state.chatListListener = null;
            }

            state.chatMessagesListeners.forEach(l => {
                if (l != null && l != undefined) {
                    l();
                }
            });

            state.chatMessagesListeners.splice(0, state.chatMessagesListeners.length);
        },
        addChatMessagesListener(state, listener) {
            state.chatMessagesListeners.push(listener);
        },
        addChat(state, chat) {
            if (!state.chats[chat.id] || chat.messages.length > state.chats[chat.id].messages.length) {
                Vue.set(state.chats, chat.id, chat);
            }
        },
        updateChatMessages(state, data) {
            for (let i = state.chats[data.chatID].messages.length - 1; i >= 0; i--) {

                let index = data.messages.findIndex(function (mMessage) {
                    return mMessage.id === state.chats[data.chatID].messages[i].id;
                });

                if (index != -1) {
                    Vue.set(state.chats[data.chatID].messages, i, data.messages[index]);
                    data.messages.splice(index, 1);
                    if (data.messages.length == 0) {
                        break;
                    }
                }
            }
        },
        sortChatMessagesByDate(state, chatID) {

            function compareChatMessagesByDate(a, b) {
                let dateA = a.date;
                let dateB = b.date;

                if (dateA === null) {
                    return 1;
                } else if (dateB === null) {
                    return -1;
                }
                if (dateB < dateA) {
                    return 1;
                }
                if (dateB > dateA) {
                    return -1;
                }
                return 0;
            }

            state.chats[chatID].messages.sort(compareChatMessagesByDate);
        },
        addChatToShow(state, chatID) {
            if (-1 == state.chatsToShowIDs.findIndex(id => id == chatID)) {
                state.chatsToShowIDs.push(chatID);
            }
        },
        sortChatsToShowByLastMessage(state) {
            function compareChatsByLastMessageDate(a, b) {
                let chatALastMessageDate = null;
                let chatBLastMessageDate = null;

                if(state.chats[a].messages[state.chats[a].messages.length - 1]){
                    chatALastMessageDate = state.chats[a].messages[state.chats[a].messages.length - 1].date;
                }

                if(state.chats[b].messages[state.chats[b].messages.length - 1]){
                    chatBLastMessageDate = state.chats[b].messages[state.chats[b].messages.length - 1].date;
                }

                if (chatALastMessageDate < chatBLastMessageDate) {
                    return 1;
                }
                if (chatALastMessageDate > chatBLastMessageDate) {
                    return -1;
                }
                return 0;
            }

            state.chatsToShowIDs.sort(compareChatsByLastMessageDate);
        },

        setUsernameFilter(state, usernameFilter) {
            state.usernameFilter = usernameFilter;
        },

        setChatListListener(state, chatListListener) {
            state.chatListListener = chatListListener;
        },

        pushChatMessages(state, data) {
            data.messages.forEach((mp) => {
                if (mp.date != null && mp.date != undefined) {
                    if (state.chats[data.chatID].messages.length == 0) {
                        Vue.set(state.chats[data.chatID].messages, 0, mp);
                    } else {
                        let alreadyAdded = false;

                        for (let i = state.chats[data.chatID].messages.length - 1; i >= 0; i--) {
                            if (mp.date != null && mp.date != undefined && (
                                (state.chats[data.chatID].messages[i].date < mp.date && !state.chats[data.chatID].messages[i + 1] && state.chats[data.chatID].messages[i].id != mp.id)
                                || (state.chats[data.chatID].messages[i].date < mp.date && state.chats[data.chatID].messages[i + 1].date > mp.date && state.chats[data.chatID].messages[i].id != mp.id && state.chats[data.chatID].messages[i + 1].id != mp.id))) {
                                state.chats[data.chatID].messages.splice(i + 1, 0, mp);
                                alreadyAdded = true;
                                break;
                            } else if (mp.date != null && mp.date != undefined && i == 0 && state.chats[data.chatID].messages[i].date > mp.date && state.chats[data.chatID].messages[i].id != mp.id) {
                                state.chats[data.chatID].messages.splice(i, 0, mp);
                                alreadyAdded = true;
                                break;
                            }
                        }

                        if (!alreadyAdded && state.chats[data.chatID].messages.length == 0) {
                            state.chats[data.chatID].messages.push(mp);
                        }

                    }
                }
            });

            //state.chats[data.chatID].messages = state.chats[data.chatID].messages.concat(data.messages);
        }
    },
    actions: {
        //----------------------------------------------------------------------------->
        // ~ resetState ~                                                              >
        // To reset state info                                                         >
        //----------------------------------------------------------------------------->
        resetState(context) {
            return new Promise(function (resolve) {
                context.commit('resetState');
                resolve();
            });
        },

        //----------------------------------------------------------------------------->
        // ~ initState ~                                                               >
        // To init chats state info with the appropriate data                          >
        //----------------------------------------------------------------------------->
        initState(context) {
            return new Promise(function (resolve, reject) {

                context.dispatch('createChatListeners', context.getters['user/getChats'])
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ manageChatListChange ~                                                    >
        // To manage the event of changes in a chat-list                               >
        // parameters required: user whose chat list we are managing                   >
        //----------------------------------------------------------------------------->
        manageChatListChange(context, user) {
            return new Promise(async function (resolve, reject) {

                for (const chatID of user.chats) {
                    if (-1 == context.getters.getChatsToShowIDs.findIndex(id => id == chatID) &&
                        (!context.state.chats.hasOwnProperty(chatID) || context.state.chats[chatID].messages.length == 0)) {
                        // It is new
                        await context.dispatch('database/getChatByID', chatID)
                            .then(async (chat) => {
                                chat = {
                                    ...chat,
                                    'messages': []
                                }

                                let userID;

                                if (chat.users[0] == context.getters['user/getID']) {
                                    userID = chat.users[1];
                                } else {
                                    userID = chat.users[0];
                                }

                                await context.dispatch('users/getUserByID', userID)
                                    .then(() => {
                                        context.commit("addChat", chat);
                                    })
                                    .catch((error) => {
                                        reject(error);
                                    });


                                // Create messages listener
                                await context.dispatch('database/createChatMessagesListener', chatID)
                                    .then(() => {
                                        context.commit("addChatToShow", chat.id);
                                        context.commit("sortChatsToShowByLastMessage");
                                    })
                                    .catch((error) => {
                                        reject(error);
                                    });

                            })
                            .catch((error) => {
                                reject(error)
                            });

                    }
                }
                resolve();
            });
        },

        //----------------------------------------------------------------------------->
        // ~ manageChatMessagesChange ~                                                >
        // To manage the event of changes in user chat messages                        >
        // parameters required: chatMessages                                           >
        //----------------------------------------------------------------------------->
        manageChatMessagesChange(context, chatMessages) {
            return new Promise(function (resolve) {
                    context.commit('addChatMessagesListener', chatMessages.listener);

                    let newMessages = [];
                    let modifiedMessagesToRead = [];
                    let modifiedMessagesDated = [];

                    chatMessages.messages.forEach((m) => {
                        if (m.change == "added" && m.date) {
                            newMessages.push(m);
                        } else if (m.change == "modified" && m.read && m.date) {
                            modifiedMessagesToRead.push(m);
                        } else if (m.change == "modified" && !m.read && m.date) {
                            modifiedMessagesDated.push(m);
                        }
                    });

                    if (newMessages.length > 0) {
                        context.commit('pushChatMessages', {chatID: chatMessages.id, messages: newMessages});
                        //context.commit("sortChatMessagesByDate", chatMessages.id);
                    }
                    if (modifiedMessagesDated.length > 0) {
                        context.commit('pushChatMessages', {chatID: chatMessages.id, messages: modifiedMessagesDated});
                        //context.commit("sortChatMessagesByDate", chatMessages.id);
                    }
                    if (modifiedMessagesToRead.length > 0) {
                        context.commit('updateChatMessages', {chatID: chatMessages.id, messages: modifiedMessagesToRead});
                    }

                    context.commit("sortChatsToShowByLastMessage");
                    resolve();
                }
            );
        },

        //----------------------------------------------------------------------------->
        // ~ setChatMessagesToReadByChatID ~                                           >
        // To set messages in chat to read state                                       >
        // parameters required: chatID                                                 >
        //----------------------------------------------------------------------------->
        setChatMessagesToReadByChatID(context, chatID) {
            return new Promise(function (resolve, reject) {

                let unreadMessages = context.getters.getUnreadMessagesByChatID(chatID);

                context.dispatch('database/setMessagesToRead', {chatID: chatID, messages: unreadMessages})
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ sendNewChatMessage ~                                                      >
        // To send a chat message                                                      >
        // parameters required: {chatID, users, messageText}                           >
        //----------------------------------------------------------------------------->
        sendNewChatMessage(context, data) {
            return new Promise(function (resolve, reject) {
                let chat = context.getters.getChatByID(data.chatID);

                let result;

                if (context.getters.getChatByID(data.chatID).messages.length == 0) {
                    result = context.dispatch('database/addChat', {id: data.chatID, users: chat.users});
                } else {
                    result = Promise.resolve();
                }

                result.then(() => {
                    context.dispatch('database/addMessagesToChat', {
                        chatID: data.chatID,
                        message: {sender: context.getters['user/getID'], text: data.messageText}
                    }).then(function () {
                        resolve();
                    }).catch((error) => {
                        reject(error);
                    });
                }).catch((error) => {
                    reject(error);
                });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ createChatListeners ~                                                     >
        // To create listeners for the chats of the user                               >
        //----------------------------------------------------------------------------->
        createChatListeners: function (context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/createAccountListener', context.getters['user/getID'])
                    .then(() => {
                        if (context.state.chatListListener != null && context.state.chatListListener != undefined) {
                            context.state.chatListListener();
                        }
                        context.commit('setChatListListener', user.listener);
                        resolve();
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        }
    }
}