import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import Constants from "../Constants";
import database from "./database";
import permissions from "./permissions";

////////////////////////////////////////////////////////
//-----                APP USERS                 -----//
////////////////////////////////////////////////////////

export default {
    namespaced: true,
    modules: {
        database,
        permissions
    },
    state: {
        /* All users retrieved */
        users: {},

        /* User IDs result of the searching process */
        searchedUsersIDs: [],
        lastUserShowed: null,
        genderFilter: "O",
        ageRangeFilter: [Constants.MIN_USER_AGE, Constants.MAX_USER_AGE],
        languageFilter: {
            filterBy: "languageToLearn",
            knownLanguage: "all",
            languageToLearn: "all"
        },
        searchByUsernameFilter: null,
        maxDistanceFilter: 3,
        maxDistanceRanges: [1, 5, 10, null],
        maxDistanceRangesText: ['1 km', '5 km', '10 km', ''],
        locationFilter: null,
        nearbyUsersIDs: null,

        /* Filtering fields */
        distanceSearchFilter: null
},
    getters: {

        getUsers: (state) => {
            return state.users;
        },
        getLanguagesByUserID: (state) => (id) => {
            return state.users[id].knownLanguages.concat(state.users[id].languagesToLearn);
        },
        getGenderFilter: (state) => {
            return state.genderFilter;
        },
        getAgeRangeFilter: (state) => {
            return state.ageRangeFilter;
        },
        getLanguageFilter: (state) => {
            return state.languageFilter;
        },
        getSearchByUsernameFilter: (state) => {
            return state.searchByUsernameFilter;
        },
        getMaxDistanceFilter: (state) => {
            return state.maxDistanceFilter;
        },
        getMaxDistanceRanges: (state) => {
            return state.maxDistanceRanges;
        },
        getMaxDistanceRangesText: (state) => {
            return state.maxDistanceRangesText;
        },
        getUserByID: (state) => (id) => {
            return state.users[id];
        },
        getNearbyUsersIDs: (state) => {
            return state.nearbyUsersIDs;
        },
        getNearbyUsersIDsLength: (state) => {
            return state.nearbyUsersIDs.length;
        },
        areThereFiltersOn: (state, getters) => {
            let filters = getters.getFilters;
            let filtersOn = false;

            if (filters.gender === "M" || filters.gender === "W"
                || filters.ageRange[0] > Constants.MIN_USER_AGE
                || filters.ageRange[1] < Constants.MAX_USER_AGE
                || (filters.language.filterBy == "languageToLearn" && filters.language.languageToLearn != "all")
                || (filters.language.filterBy == "knownLanguage" && filters.language.knownLanguage != "all")
                || filters.maxDistance != null) {
                filtersOn = true;
            }

            return filtersOn;
        },
        getFilters: (state) => {
            return {
                gender: state.genderFilter,
                ageRange: state.ageRangeFilter,
                language: state.languageFilter,
                maxDistance: state.maxDistanceRanges[state.maxDistanceFilter],
                location: state.locationFilter,
                username: state.searchByUsernameFilter
            };
        },
        getLastUserShowed: (state) => {
            return state.lastUserShowed;
        },
        getSearchedUsers: (state) => {

            let searchedUsersReturn = [];

            state.searchedUsersIDs.forEach(userID => {
                searchedUsersReturn.push({
                    id: userID,
                    ...state.users[userID]
                });
            });

            return searchedUsersReturn;
        },
        getSearchedUsersIDsLength: (state) => {
            return state.searchedUsersIDs.length;
        },
        getSearchedUsersIDs: (state) => {
            return state.searchedUsersIDs;
        }
},
    mutations: {
        resetState(state) {
            /* All users retrieved */
            state.users = {};

            /* User IDs result of the searching process */
            state.searchedUsersIDs = [];
            state.lastUserShowed = null;
            state.genderFilter = "O";
            state.ageRangeFilter = [Constants.MIN_USER_AGE, Constants.MAX_USER_AGE];
            state.languageFilter = {
                'filterBy': "languageToLearn",
                'knownLanguage': "all",
                'languageToLearn': "all"
            };
            state.searchByUsernameFilter = null;
            state.maxDistanceFilter = 3;
            state.maxDistanceRanges = [1000, 5000, 10000, null];
            state.maxDistanceRangesText = ['1 km', '5 km', '10 km', ''];
            state.locationFilter = null;
            state.nearbyUsersIDs = null;

            /* Filtering fields */
            state.distanceSearchFilter = null;
        },
        setUsers(state, users) {
            state.users = users;
        },
        setGenderFilter: (state, genderFilter) => {
            state.genderFilter = genderFilter;
        },
        setAgeRangeFilter: (state, ageRangeFilter) => {
            state.ageRangeFilter = ageRangeFilter;
        },
        setNearbyUsersIDs: (state, nearbyUsersIDs) => {
            state.nearbyUsersIDs = nearbyUsersIDs;
        },
        setLanguageFilter: (state, languageFilter) => {
            state.languageFilter = languageFilter;
        },
        setLocationFilter: (state, locationFilter) => {
            state.locationFilter = locationFilter;
        },
        setSearchByUsernameFilter: (state, searchByUsernameFilter) => {
            state.searchByUsernameFilter = searchByUsernameFilter;
        },
        setMaxDistanceFilter: (state, maxDistanceFilter) => {
            state.maxDistanceFilter = maxDistanceFilter;
        },
        addUser(state, user) {
            state.users[user.id] = user;
        },
        addSearchedUserID(state, userID) {
            if (-1 == state.searchedUsersIDs.findIndex(id => {
                return id == userID;
            })) {
                state.searchedUsersIDs.push(userID);
            }
        },
        resetSearchedUsersIDs(state) {
            state.searchedUsersIDs = [];
        },
        setLastUserShowed(state, lastUserShowed) {
            state.lastUserShowed = lastUserShowed;
        },
        addRating(state, data) {
            let userTo = state.users[data.id];

            if (data.prev == null) {
                //new rate
                let newAvgRating = userTo.avgRating * userTo.numRatings;
                newAvgRating += data.rating;
                newAvgRating /= (userTo.numRatings + 1);
                state.users[data.id].avgRating = newAvgRating;
                state.users[data.id].numRatings = userTo.numRatings + 1;
            } else {
                if (data.rating == null) {
                    //remove rate
                    let newAvgRating = userTo.avgRating * userTo.numRatings;
                    newAvgRating = newAvgRating - data.prev;
                    if ((userTo.numRatings - 1) != 0)
                        newAvgRating /= (userTo.numRatings - 1);
                    state.users[data.id].avgRating = newAvgRating;
                    state.users[data.id].numRatings = userTo.numRatings - 1;
                } else {
                    //modify rate
                    let newAvgRating = userTo.avgRating * userTo.numRatings;
                    newAvgRating = newAvgRating + data.rating - data.prev;
                    if (userTo.numRatings != 0)
                        newAvgRating /= userTo.numRatings;
                    state.users[data.id].avgRating = newAvgRating;
                }

            }
        }
},
    actions: {
        //----------------------------------------------------------------------------->
        // ~ resetState ~                                                              >
        // To reset state info                                                         >
        //----------------------------------------------------------------------------->
        resetState(context) {
            context.commit('resetState');
        },

        //----------------------------------------------------------------------------->
        // ~ getSearchedUsers ~                                                        >
        // To list searched users using pagination                                     >
        // return: true/false (are there more users to show?)                          >
        //----------------------------------------------------------------------------->
        getSearchedUsers(context) {
            return new Promise(function (resolve, reject) {
                let filters = context.getters.getFilters;

                if (filters.location != null && filters.location != undefined && filters.maxDistance != null) {
                    if (context.getters.getLastUserShowed == null) {
                        context.dispatch('database/getNearbyUserIDs', {
                            location: filters.location,
                            maxDistance: filters.maxDistance
                        })
                            .then((ids) => {
                                context.commit('setNearbyUsersIDs', ids);
                                context.dispatch('getNearbyUsersProfiles')
                                    .then(() => {
                                        context.dispatch('areThereMoreNearbyUsers')
                                            .then((areThereMore) => {
                                                resolve(areThereMore);
                                            })
                                            .catch((error) => {
                                                reject(error);
                                            });
                                    })
                                    .catch((error) => {
                                        reject(error);
                                    });
                            })
                            .catch((error) => {
                                reject(error);
                            });
                    } else {
                        context.dispatch('getNearbyUsersProfiles')
                            .then(() => {
                                context.dispatch('areThereMoreNearbyUsers')
                                    .then((areThereMore) => {
                                        resolve(areThereMore);
                                    })
                                    .catch((error) => {
                                        reject(error);
                                    });
                            })
                            .catch((error) => {
                                reject(error);
                            });
                    }
                } else {
                    context.dispatch('database/getNextUsersPage', {
                        lastUserShowed: context.getters.getLastUserShowed,
                        limit: Constants.SEARCHED_USERS_PAGE_SIZE,
                        filters: filters
                    })
                        .then((data) => {
                            context.commit('setLastUserShowed', data.newLastUserShowed);

                            data.users.forEach(user => {
                                context.commit('addUser', user);
                                context.commit('addSearchedUserID', user.id);
                            });
                            if (data.users.length > 0) {

                                context.dispatch('database/isNextUsersPageEmpty', {
                                    lastUserShowed: context.getters.getLastUserShowed,
                                    limit: Constants.SEARCHED_USERS_PAGE_SIZE,
                                    filters: filters
                                })
                                    .then((isEmpty) => {
                                        if (data.users.length > 0 && data.users.length < Constants.SEARCHED_USERS_PAGE_SIZE
                                            && !isEmpty) {
                                            context.dispatch('database/getNextUsersPage', {
                                                lastUserShowed: context.getters.getLastUserShowed,
                                                limit: Constants.SEARCHED_USERS_PAGE_SIZE - data.users.length,
                                                filters: filters
                                            })
                                                .then((data2) => {
                                                    context.commit('setLastUserShowed', data2.newLastUserShowed);
                                                    data2.users.forEach(user => {
                                                        context.commit('addUser', user);
                                                        context.commit('addSearchedUserID', user.id);
                                                    });
                                                    context.dispatch('database/isNextUsersPageEmpty', {
                                                        lastUserShowed: context.getters.getLastUserShowed,
                                                        limit: Constants.SEARCHED_USERS_PAGE_SIZE,
                                                        filters: filters
                                                    })
                                                        .then((isEmpty2) => {
                                                            resolve(!isEmpty2);
                                                        })
                                                        .catch((error) => {
                                                            reject(error);
                                                        });
                                                }).catch((error) => {
                                                reject(error);
                                            });
                                        } else {
                                            resolve(!isEmpty);
                                        }
                                    })
                                    .catch((error) => {
                                        reject(error);
                                    });
                            }
                        })
                        .catch((error) => {
                            reject(error);
                        });
                }
            });
        },

        //----------------------------------------------------------------------------->
        // ~ areThereMoreNearbyUsers ~                                                 >
        // To check if there are valid nearby users to show yet                        >
        // return: true/false (are there more valid nearby users to show?)             >
        //----------------------------------------------------------------------------->
        areThereMoreNearbyUsers(context) {
            return new Promise(async function (resolve, reject) {
                let allIDs = context.getters.getNearbyUsersIDs;
                let filters = context.getters.getFilters;
                let lastUserShowed = context.getters.getLastUserShowed;
                let i;

                while (lastUserShowed + 1 < context.getters.getNearbyUsersIDsLength) {
                    if (lastUserShowed == null) {
                        i = 0;
                    } else {
                        i = lastUserShowed + 1;
                    }

                    let idsToGetProfiles = [];

                    for (; i < Constants.SEARCHED_USERS_PAGE_SIZE + lastUserShowed + 1; i++) {
                        if (i == context.getters.getNearbyUsersIDsLength)
                            break;
                        idsToGetProfiles.push(allIDs[i]);

                    }

                    lastUserShowed = i - 1;

                    await context.dispatch('database/getMultipleProfilesByUID', idsToGetProfiles)
                        .then((userProfiles) => {
                            userProfiles.forEach(user => {
                                let isValid = true;

                                // --> USERNAME FILTERING
                                if (filters.username != null && filters.username != undefined && filters.username != ''
                                    && !user.username.startsWith(filters.username)) {
                                    isValid = false;
                                }
                                // --> END USERNAME FILTERING

                                // --> GENDER FILTERING
                                if ((filters.gender === "M" && user.gender !== "M")
                                    || (filters.gender === "W" && user.gender !== "W")) {
                                    isValid = false;
                                }
                                // --> END GENDER FILTERING

                                // --> AGE FILTERING
                                if (filters.ageRange[0] > Constants.MIN_USER_AGE || filters.ageRange[1] < Constants.MAX_USER_AGE) {
                                    if (filters.ageRange[0] > Constants.MIN_USER_AGE) {
                                        let currentDate = new Date();
                                        currentDate.setFullYear(currentDate.getFullYear() - filters.ageRange[0]);
                                        currentDate.setHours(0, 0, 0);
                                        if (new Date(user.birthDate.toDate().setHours(0, 0, 0)) >= new Date(currentDate)) {
                                            isValid = false;
                                        }
                                    }

                                    if (filters.ageRange[1] < Constants.MAX_USER_AGE) {
                                        let currentDate = new Date();
                                        currentDate.setFullYear(currentDate.getFullYear() - filters.ageRange[1] - 1);
                                        currentDate.setHours(0, 0, 0)
                                        if (new Date(user.birthDate.toDate().setHours(0, 0, 0)) <= new Date(currentDate)) {
                                            isValid = false;
                                        }
                                    }
                                }
                                // --> END AGE FILTERING

                                // --> LANGUAGES FILTERING
                                if (filters.language.filterBy == "languageToLearn" && filters.language.languageToLearn != "all") {

                                    let languageToLearnIndex = user.languagesToLearn.findIndex(function (lang) {
                                        return lang == filters.language.languageToLearn;
                                    });

                                    if (languageToLearnIndex == -1) {
                                        isValid = false;
                                    }
                                } else if (filters.language.filterBy == "knownLanguage" && filters.language.knownLanguage != "all") {
                                    let knownLanguageIndex = user.knownLanguages.findIndex(function (lang) {
                                        return lang == filters.language.knownLanguage;
                                    });

                                    if (knownLanguageIndex == -1) {
                                        isValid = false;
                                    }
                                }
                                // --> END LANGUAGES FILTERING

                                if (!user.registrationCompleted) {
                                    isValid = false;
                                }

                                if (isValid) {
                                    resolve(true);
                                }
                            });
                        })
                        .catch((error) => {
                            reject(error);
                        });
                }

                resolve(false);
            });
        },

        //----------------------------------------------------------------------------->
        // ~ getNearbyUsersProfiles ~                                                  >
        // To show valid nearby users with pagination                                  >
        //----------------------------------------------------------------------------->
        getNearbyUsersProfiles(context) {
            return new Promise(async function (resolve, reject) {
                let usersAdded = 0;
                let allIDs = context.getters.getNearbyUsersIDs;
                let filters = context.getters.getFilters;

                while (usersAdded < Constants.SEARCHED_USERS_PAGE_SIZE && context.getters.getLastUserShowed + 1 < context.getters.getNearbyUsersIDsLength) {
                    let i;
                    let lastUserShowed = context.getters.getLastUserShowed;

                    if (lastUserShowed == null) {
                        i = 0;
                        lastUserShowed = 0;
                    } else {
                        i = lastUserShowed + 1;
                    }

                    let idsToGetProfiles = [];
                    for (; i < Constants.SEARCHED_USERS_PAGE_SIZE + lastUserShowed; i++) {
                        if (i == context.getters.getNearbyUsersIDsLength)
                            break;

                        idsToGetProfiles.push(allIDs[i]);
                    }

                    context.commit('setLastUserShowed', i - 1);

                    await context.dispatch('database/getMultipleProfilesByUID', idsToGetProfiles)
                        .then(async (userProfiles) => {
                            userProfiles.forEach(user => {
                                let isValid = true;

                                // --> USERNAME FILTERING
                                if (filters.username != null && filters.username != undefined && filters.username != ''
                                    && !user.username.startsWith(filters.username)) {
                                    isValid = false;
                                }
                                // --> END USERNAME FILTERING

                                // --> GENDER FILTERING
                                if ((filters.gender === "M" && user.gender !== "M")
                                    || (filters.gender === "W" && user.gender !== "W")) {
                                    isValid = false;
                                }
                                // --> END GENDER FILTERING

                                // --> AGE FILTERING
                                if (filters.ageRange[0] > Constants.MIN_USER_AGE || filters.ageRange[1] < Constants.MAX_USER_AGE) {
                                    if (filters.ageRange[0] > Constants.MIN_USER_AGE) {
                                        let currentDate = new Date();
                                        currentDate.setFullYear(currentDate.getFullYear() - filters.ageRange[0]);
                                        currentDate.setHours(0, 0, 0);
                                        if (new Date(user.birthDate.toDate().setHours(0, 0, 0)) >= new Date(currentDate)) {
                                            isValid = false;
                                        }
                                    }

                                    if (filters.ageRange[1] < Constants.MAX_USER_AGE) {
                                        let currentDate = new Date();
                                        currentDate.setFullYear(currentDate.getFullYear() - filters.ageRange[1] - 1);
                                        currentDate.setHours(0, 0, 0)
                                        if (new Date(user.birthDate.toDate().setHours(0, 0, 0)) <= new Date(currentDate)) {
                                            isValid = false;
                                        }
                                    }
                                }
                                // --> END AGE FILTERING

                                // --> LANGUAGES FILTERING
                                if (filters.language.filterBy == "languageToLearn" && filters.language.languageToLearn != "all") {

                                    let languageToLearnIndex = user.languagesToLearn.findIndex(function (lang) {
                                        return lang == filters.language.languageToLearn;
                                    });

                                    if (languageToLearnIndex == -1) {
                                        isValid = false;
                                    }
                                } else if (filters.language.filterBy == "knownLanguage" && filters.language.knownLanguage != "all") {
                                    let knownLanguageIndex = user.knownLanguages.findIndex(function (lang) {
                                        return lang == filters.language.knownLanguage;
                                    });

                                    if (knownLanguageIndex == -1) {
                                        isValid = false;
                                    }
                                }
                                // --> END LANGUAGES FILTERING

                                if (isValid) {
                                    context.commit('addUser', user);
                                    context.commit('addSearchedUserID', user.id);
                                    usersAdded++;
                                }
                            });
                        })
                        .catch((error) => {
                            reject(error);
                        });
                }
                resolve();
            });
        },


        //----------------------------------------------------------------------------->
        // ~ resetSearchedUsers ~                                                      >
        // To reset variables related to user searching                                >
        //----------------------------------------------------------------------------->
        resetSearchedUsers(context) {
            return new Promise(function (resolve) {
                context.commit("resetSearchedUsersIDs");
                context.commit('setLastUserShowed', null);

                resolve();
            });
        },

        //----------------------------------------------------------------------------->
        // ~ setFilters ~                                                              >
        // To set the filtering values selected by the user for the searching          >
        //----------------------------------------------------------------------------->
        setFilters(context, filters) {
            return new Promise(function (resolve) {
                context.commit("setGenderFilter", filters.gender);
                context.commit("setAgeRangeFilter", filters.ageRange);
                context.commit("setLanguageFilter", filters.language);
                context.commit("setMaxDistanceFilter", filters.maxDistance);
                context.commit("setLocationFilter", filters.location);
                resolve();
            });
        },


        //----------------------------------------------------------------------------->
        // ~ getUserByID ~                                                             >
        // To user profile info by its ID                                              >
        // parameters required: id                                                     >
        //----------------------------------------------------------------------------->
        getUserByID(context, id) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/getProfileByUID', id)
                    .then((profileInfo) => {
                        if (profileInfo) {
                            context.commit('addUser', profileInfo);
                            resolve(profileInfo);
                        } else {
                            resolve(false);
                        }
                    })
                    .catch((error) => {
                        console.log("Error getting user by it's id in firebase.");
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ resetFilters ~                                                            >
        // To reset the filtering values to default                                    >
        //----------------------------------------------------------------------------->
        resetFilters(context) {
            return new Promise(function (resolve) {
                context.commit("setGenderFilter", "O");
                context.commit("setAgeRangeFilter", [Constants.MIN_USER_AGE, Constants.MAX_USER_AGE]);
                context.commit('setLanguageFilter', {
                    filterBy: "languageToLearn",
                    knownLanguage: "all",
                    languageToLearn: "all"
                });

                context.commit("setLocationFilter", null);
                context.commit("setNearbyUsersIDs", null);
                context.commit("setNearbyUsersIDs", null);
                context.commit("setMaxDistanceFilter", 3)
                context.commit("setSearchByUsernameFilter", null)

                let filters = context.getters.getFilters;
                filters["maxDistance"] = null;

                resolve(filters);
            });
        }
}
}