import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import users from "./modules/users";
import events from "./modules/events";
import user from "./modules/user";
import chats from "./modules/chats";
import ranking from "./modules/ranking";
import database from "./modules/database";
import permissions from "./modules/permissions";

export default new Vuex.Store({
    modules: {
        user,
        users,
        events,
        chats,
        ranking,
        database,
        permissions
    },
    state: {
        appInfoLoaded: false
},
    getters: {
        getAppInfoLoaded: (state) => {
            return state.appInfoLoaded;
        }
},
    mutations: {
        setAppInfoLoaded(state, appInfoLoaded) {
            state.appInfoLoaded = appInfoLoaded;
        }
},
    actions: {
        //----------------------------------------------------------------------------->
        // ~ getLoggedUser ~                                                           >
        // To get user logged                                                          >
        //----------------------------------------------------------------------------->
        getLoggedUser: function (context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/isUserAuth')
                    .then((user) => {
                        resolve(user);
                    })
                    .catch((error) => {
                        console.error(error);
                        reject(error);
                    });
            });
        },


        //----------------------------------------------------------------------------->
        // ~ resetState ~                                                              >
        // To reset all modules state info                                             >
        //----------------------------------------------------------------------------->
        resetAppState: function (context) {
            return new Promise(function (resolve, reject) {
                if (context.getters.getAppInfoLoaded) {
                    let promises = [];

                    promises.push(context.dispatch('user/resetState'));
                    promises.push(context.dispatch('users/resetState'));
                    promises.push(context.dispatch('chats/resetState'));
                    promises.push(context.dispatch('events/resetState'));
                    promises.push(context.dispatch('ranking/resetState'));
                    promises.push(context.dispatch('permissions/resetState'));

                    Promise.all(promises).then(() => {
                        console.log('state successfully reseted.');
                        context.commit('setAppInfoLoaded', false);
                        resolve();
                    })
                        .catch((error) => {
                            console.error(error);
                            console.log('Error resetting state.');
                            reject(this.$i18n.t('messages.ERROR_GENERIC_PROBLEM'));
                        });
                } else {
                    resolve();
                }
            });
        },


        //----------------------------------------------------------------------------->
        // ~ initAppInfo ~                                                             >
        // To init the necessary info to init the app for a logged user                >
        //----------------------------------------------------------------------------->
        initAppInfo: function (context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('user/isUserLogged')
                    .then((isUserLogged) => {
                        if (isUserLogged && !context.getters.getAppInfoLoaded) {
                            context.dispatch('user/initState')
                                .then(() => {
                                    console.log('user module state successfully initiated.');
                                    context.dispatch('chats/initState')
                                        .then(() => {
                                            console.log('chats module state successfully initiated.');
                                            context.dispatch('ranking/initState')
                                                .then(() => {
                                                    console.log('ranking module state successfully initiated.');
                                                    context.dispatch('events/initState')
                                                        .then(() => {
                                                            console.log('events module state successfully initiated.');
                                                            context.commit('setAppInfoLoaded', true);
                                                            resolve();
                                                        })
                                                        .catch((error) => {
                                                            reject(error);
                                                        });
                                                })
                                                .catch((error) => {
                                                    reject(error);
                                                });
                                        })
                                        .catch((error) => {
                                            reject(error);
                                        });

                                })
                                .catch((error) => {
                                    reject(error);
                                });
                        } else {
                            resolve();
                        }

                    })
                    .catch((error) => {
                        console.log('Error initiating users module state.');
                        reject(error);
                    });
            })

        }
}
});
