import Vue from 'vue'
import Router from 'vue-router'
import firebase from "firebase";
import Home from './views/Home.vue'
import store from './store/store.js'

Vue.use(Router)

const router = new Router({
    /*mode: 'history',*/
    base: process.env.BASE_URL,
    routes: [
        {
            //To redirect all routes that do not exist to /home
            path: '*',
            redirect: '/home'
        },
        {
            //To redirect root route to /home
            path: '/',
            redirect: '/home'
        },
        {
            path: '/home',
            name: 'home',
            component: Home
        },
        {
            path: '/login',
            name: 'login',
            component: () => import('./views/Accounts/Login.vue')
        },
        {
            path: '/sign-up',
            name: 'signUp',
            component: () => import('./views/Accounts/SignUp.vue')
        },
        {
            path: '/email-verification',
            name: 'emailVerification',
            component: () => import('./views/Accounts/EmailVerification.vue'),
            meta: {
                requiresAuth: true
            }
        }, {
            path: '/fill-your-profile-info',
            name: 'fillYourProfileInfo',
            component: () => import('./views/Accounts/FillYourProfileInfo.vue'),
            meta: {
                requiresAuth: true
            }
        }, {
            path: '/fill-your-languages',
            name: 'fillYourLanguages',
            component: () => import('./views/Accounts/FillYourLanguages.vue'),
            meta: {
                requiresAuth: true
            }
        }, {
            path: '/set-your-profile-image',
            name: 'setYourProfileImage',
            component: () => import('./views/Accounts/SetYourProfileImage.vue'),
            meta: {
                requiresAuth: true
            }
        }, {
            path: '/send-change-password-email',
            name: 'sendChangePasswordEmail',
            component: () => import('./views/Accounts/SendChangePasswordEmail.vue'),
            meta: {
                requiresAuth: false
            }
        },
        {
            path: '/dashboard',
            name: 'dashboard',
            redirect: {name: 'profileHome'},
            meta: {requiresAuth: true},
            component: () => import('./views/Dashboard.vue'),
            children: [
                {
                    path: 'ranking',
                    name: 'rankingHome',
                    meta: {requiresAuth: true, menu: 'ranking'},
                    component: () => import('./views/Ranking/RankingHome.vue')
                },
                {
                    path: 'ranking/user/:id/',
                    name: 'userProfileRanking',
                    meta: {requiresAuth: true, menu: 'ranking'},
                    component: () => import('./views/UserProfile.vue')
                },
                {
                    path: 'events',
                    name: 'eventsHome',
                    meta: {requiresAuth: true, menu: 'events'},
                    component: () => import('./views/Events/EventsHome.vue')
                },
                {
                    path: 'event/:id/',
                    name: 'eventDescription',
                    meta: {requiresAuth: true, menu: 'events'},
                    component: () => import('./views/Events/EventDescription.vue')
                },
                {
                    path: 'event/:id/edit',
                    name: 'eventEdit',
                    meta: {requiresAuth: true, menu: 'events'},
                    component: () => import('./views/Events/EventEdit.vue')
                },
                {
                    path: 'events/create',
                    name: 'eventCreate',
                    meta: {requiresAuth: true, menu: 'events'},
                    component: () => import('./views/Events/EventCreate.vue')
                },
                {
                    path: 'search',
                    name: 'searchHome',
                    meta: {requiresAuth: true, menu: 'search'},
                    component: () => import('./views/Search/SearchHome.vue')
                },
                {
                    path: 'search/filter',
                    name: 'searchFilter',
                    meta: {requiresAuth: true, menu: 'search'},
                    component: () => import('./views/Search/SearchFilter.vue')
                },
                {
                    path: 'search/user/:id/',
                    name: 'userProfileSearch',
                    meta: {requiresAuth: true, menu: 'search'},
                    component: () => import('./views/UserProfile.vue')
                },
                {
                    path: 'chats',
                    name: 'chatHome',
                    meta: {requiresAuth: true, menu: 'chats'},
                    component: () => import('./views/Chats/ChatHome.vue')
                },
                {
                    path: 'chats/chat/:id/',
                    name: 'chatWithUser',
                    meta: {requiresAuth: true, menu: 'chats'},
                    component: () => import('./views/Chats/ChatWithUser.vue')
                },
                {
                    path: 'chats/user/:id/',
                    name: 'userProfileChat',
                    meta: {requiresAuth: true, menu: 'chats'},
                    component: () => import('./views/UserProfile.vue')
                },
                {
                    path: 'profile',
                    name: 'profileHome',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Profile/ProfileHome.vue')
                },
                {
                    path: 'profile/:id/edit',
                    name: 'profileEdit',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Profile/ProfileEdit.vue')
                },
                {
                    path: 'settings',
                    name: 'settings',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Settings/SettingsHome.vue')
                },
                {
                    path: 'settings/account',
                    name: 'settingsAccount',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Settings/SettingsAccount.vue')
                },
                {
                    path: 'settings/account/edit/name-and-last-name',
                    name: 'settingsAccountEditNameAndLastName',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Settings/SettingsAccountEditNameAndLastName.vue')
                },
                {
                    path: 'settings/account/edit/email',
                    name: 'settingsAccountEditEmail',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Settings/SettingsAccountEditEmail.vue')
                },
                {
                    path: 'settings/account/password-verification',
                    name: 'settingsAccountPasswordVerification',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Settings/SettingsAccountPasswordVerification.vue')
                },
                {
                    path: 'settings/account/edit/password',
                    name: 'settingsAccountEditPassword',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Settings/SettingsAccountEditPassword.vue')
                },
                {
                    path: 'settings/language',
                    name: 'settingsLanguage',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Settings/SettingsLanguage.vue')
                },
                {
                    path: 'settings/permissions',
                    name: 'settingsPermissions',
                    meta: {requiresAuth: true, menu: 'profile'},
                    component: () => import('./views/Settings/SettingsPermissions.vue')
                },
            ]
        }
    ]
});


router.beforeEach((to, from, next) => {
    const currentUser = firebase.auth().currentUser;
    const requiresAuth = to.matched.some(record => record.meta.requiresAuth);
    if (requiresAuth && !currentUser) {
        next('home');
    } else if ((requiresAuth && currentUser && (to.name !== from.name) && (!to.path.startsWith('/dashboard') || !from.path.startsWith('/dashboard'))) || (!requiresAuth && currentUser)) {
        store.dispatch('user/getRegistrationState').then(registrationState => {
            if (to.path.startsWith('/' + registrationState)) {
                next()
            } else if (from.path.startsWith('/' + registrationState)) {
                next({name: from.name});
            } else {
                next({path: '/' + registrationState});
            }
        });
        //next()
    }
    else {
        next();
    }

});

export default router;
