import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import Constants from "../Constants";
import database from "./database";
import permissions from "./permissions";
import users from "./users.js";
import user from "./user.js";
import haversine from 'haversine-distance'

////////////////////////////////////////////////////////
//-----                 EVENTS                   -----//
////////////////////////////////////////////////////////

export default {
    namespaced: true,
    modules: {
        user,
        users,
        database,
        permissions
    },
    state: {
        events: {},
        eventsToShowIDs: [],
        mapEventsToShowIDs: [],
        lastEventShowed: null,
        languageInvolvedFilter: "all",
        eventsTab: 0
    },
    getters: {
        getLanguageInvolvedFilter: (state) => {
            return state.languageInvolvedFilter;
        },
        getFilters: (state) => {
            return {
                languageInvolved: state.languageInvolvedFilter
            };
        },
        getEvents: (state) => {
            return state.events;
        },
        getEventsTab: (state) => {
            return state.eventsTab;
        },
        getEventByID: (state) => (id) => {
            return state.events[id];
        },
        getEventsToShow: (state, getters) => {
            let eventsToShow = [];
            state.eventsToShowIDs.forEach((eventID) => {
                let distance = null;
                let userLocation = getters['user/getLocation'];

                if (userLocation != null) {
                    distance = haversine(state.events[eventID].location.coordinates, userLocation);
                    distance = distance / 1000;
                }

                let nEvent = {
                    'id': eventID,
                    ...state.events[eventID]
                };

                nEvent['distance'] = distance;
                eventsToShow.push(nEvent);
            });
            return eventsToShow;
        },
        getMapEventsToShow: (state) => {
            let eventsToShow = [];
            state.mapEventsToShowIDs.forEach((eventID) => {
                eventsToShow.push({
                    id: eventID,
                    ...state.events[eventID]
                })
            });
            return eventsToShow;
        },
        getLastEventShowed: (state) => {
            return state.lastEventShowed;
        },
        amIParticipating: (state) => (data) => {

            let found = null;
            if (state.events[data.eventID]) {
                found = state.events[data.eventID].participants.find(participantID => participantID == data.userID);
                return found != undefined;
            }
            return null;

        }
    },
    mutations: {
        setLanguageInvolvedFilter(state, languageInvolvedFilter) {
            state.languageInvolvedFilter = languageInvolvedFilter;
        },
        setEventsTab(state, eventsTab) {
            state.eventsTab = eventsTab;
        },
        removeEventByID(state, eventID) {
            delete state.events[eventID];

            let indexEventsToShowIDs = state.eventsToShowIDs.findIndex(id => {
                return id == eventID;
            });
            state.eventsToShowIDs.splice(indexEventsToShowIDs, 1);

            let indexMapEventsToShowIDs = state.mapEventsToShowIDs.findIndex(id => {
                return id == eventID;
            });
            state.mapEventsToShowIDs.splice(indexMapEventsToShowIDs, 1);
        },
        resetState(state) {
            state.events = {};
            state.eventsToShowIDs.splice(0, state.eventsToShowIDs.length);
            state.mapEventsToShowIDs.splice(0, state.mapEventsToShowIDs.length);
            state.lastEventShowed = null;
            state.languageInvolvedFilter = "all";
        },
        resetEventsToShowIDs(state) {
            state.eventsToShowIDs.splice(0, state.eventsToShowIDs.length);
            state.lastEventShowed = null;
        },
        addEvent(state, event) {
            state.events[event.id] = event;
        },
        addParticipantToEvent(state, data) {
            if (-1 == state.events[data.eventID].participants.findIndex(participantID => participantID == data.userID)) {
                state.events[data.eventID].participants.push(data.userID);
            }
        },
        removeParticipantFromEvent(state, data) {
            let searchedUserIndex = state.events[data.eventID].participants.findIndex(function (userID) {
                return userID == data.userID;
            });
            state.events[data.eventID].participants.splice(searchedUserIndex, 1);
        },
        addEventToShow(state, eventID) {
            if (-1 == state.eventsToShowIDs.findIndex(id => {
                return id == eventID;
            })) {
                state.eventsToShowIDs.push(eventID);
            }
        },
        addMapEventToShow(state, eventID) {
            if (-1 == state.mapEventsToShowIDs.findIndex(id => {
                return id == eventID;
            })) {
                state.mapEventsToShowIDs.push(eventID);
            }
        },
        setEventsToShowIDs(state, eventsToShowIDs) {
            state.eventsToShowIDs = eventsToShowIDs;
        },
        setLastEventShowed(state, lastEventShowed) {
            state.lastEventShowed = lastEventShowed;
        }
    },
    actions: {
        //----------------------------------------------------------------------------->
        // ~ initState ~                                                               >
        // To init events state info with the appropriate data                         >
        //----------------------------------------------------------------------------->
        initState(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('resetState')
                    .then(() => {
                        context.dispatch('getAllEvents')
                            .then(() => {
                                resolve();
                            })
                            .catch((error) => {
                                reject(error)
                            });
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });

        },

        resetEventsToShowIDs(context) {
            context.commit('resetEventsToShowIDs');
        },

        //----------------------------------------------------------------------------->
        // ~ createEvent ~                                                             >
        // To create an event                                                          >
        // parameters required: data {date, description, headerImg, location,          >
        //                            involvedLanguages, name}                         >
        //----------------------------------------------------------------------------->
        createEvent(context, data) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/createEvent', data)
                    .then((eventData) => {
                        let eventCoordinates = {
                            'lat': data.location.coordinates._lat,
                            'lng': data.location.coordinates._long
                        }
                        eventData.location.coordinates = eventCoordinates;
                        context.commit('addEvent', eventData);
                        context.commit('addMapEventToShow', eventData.id);
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ removeEvent ~                                                             >
        // To remove an event                                                          >
        // parameters required: eventID                                                >
        //----------------------------------------------------------------------------->
        removeEvent(context, eventID) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/removeEvent', eventID)
                    .then(() => {
                        context.commit('removeEventByID', eventID);
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ updateEvent ~                                                             >
        // To update an event                                                          >
        // parameters required: data {date, description, headerImg, location,          >
        //                            involvedLanguages, name}                         >
        //----------------------------------------------------------------------------->
        updateEvent(context, data) {
            return new Promise(function (resolve, reject) {
                let dataToUpdate = {};
                let localEvent = context.getters.getEventByID(data.id);

                if (data.name != localEvent.name) {
                    dataToUpdate['name'] = data.name;
                }

                if (new Date(data.date.setHours(0, 0, 0)) != localEvent.date.toDate().toString()) {
                    dataToUpdate['date'] = data.date;
                }

                if (data.headerImg != localEvent.headerImg) {
                    dataToUpdate['headerImg'] = data.headerImg;
                }

                if (data.description != localEvent.description) {
                    dataToUpdate['description'] = data.description;
                }

                if (data.involvedLanguages.length != localEvent.involvedLanguages.length) {
                    dataToUpdate['involvedLanguages'] = data.involvedLanguages;
                } else {
                    for (let i = 0; i < data.involvedLanguages.length; i++) {
                        if (data.involvedLanguages[i] != localEvent.involvedLanguages[i]) {
                            dataToUpdate['involvedLanguages'] = data.involvedLanguages;
                            break;
                        }
                    }
                }

                if (data.location.coordinates != localEvent.location.coordinates
                    || data.location.city != localEvent.location.city
                    || data.location.place != localEvent.location.place
                    || data.location.address != localEvent.location.address) {
                    dataToUpdate['location'] = data.location;
                }


                if (Object.keys(dataToUpdate).length > 0) {
                    dataToUpdate['id'] = data.id;
                    context.dispatch('database/updateEvent', dataToUpdate)
                        .then((updatesValues) => {
                            for (let [key, value] of Object.entries(dataToUpdate)) {
                                localEvent[key] = value;
                                if (key == 'location') {
                                    let eventCoordinates = {
                                        'lat': localEvent.location.coordinates._lat,
                                        'lng': localEvent.location.coordinates._long
                                    }
                                    localEvent.location.coordinates = eventCoordinates;
                                }
                            }

                            for (let [key, value] of Object.entries(updatesValues)) {
                                if (key != 'location') {
                                    localEvent[key] = value;
                                }
                            }

                            context.commit('addEvent', localEvent);
                            resolve();
                        })
                        .catch((error) => {
                            reject(error)
                        });
                }
            });
        },

        //----------------------------------------------------------------------------->
        // ~ getEvents ~                                                               >
        // To get events that will be celebrated                                       >
        //----------------------------------------------------------------------------->
        getEvents(context) {
            return new Promise(function (resolve, reject) {
                let filters = context.getters.getFilters;

                context.dispatch('database/getNextEventsPage', {
                    lastEventShowed: context.getters.getLastEventShowed,
                    limit: Constants.EVENTS_PAGE_SIZE,
                    filters: filters
                })
                    .then((data) => {
                        context.commit('setLastEventShowed', data.newLastEventShowed);

                        data.events.forEach(event => {
                            let userLocation = context.getters['user/getLocation'];
                            let distance = null;
                            let eventLocation = {
                                'lat': event.location.coordinates._lat,
                                'lng': event.location.coordinates._long
                            }

                            let eventNonFormattedLoc = event.location;
                            eventNonFormattedLoc['coordinates'] = eventLocation;
                            event['location'] = eventNonFormattedLoc;

                            if (userLocation != null) {
                                distance = haversine(event.location.coordinates, userLocation);
                                distance = distance / 1000;
                            }

                            event = {
                                ...event,
                                'distance': distance
                            };
                            context.commit('addEvent', event);
                            context.commit('addEventToShow', event.id);
                        });

                        if (data.events.length > 0) {
                            context.dispatch('database/isNextEventsPageEmpty', {
                                lastEventShowed: context.getters.getLastEventShowed,
                                limit: Constants.EVENTS_PAGE_SIZE,
                                filters: filters
                            })
                                .then((isEmpty) => {
                                    resolve(!isEmpty);
                                })
                                .catch((error) => {
                                    reject(error);
                                });
                        } else {
                            resolve(false);
                        }
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ getAllEvents ~                                                            >
        // To get all events that will be celebrated                                   >
        //----------------------------------------------------------------------------->
        getAllEvents(context) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/getEventsToBeCelebrated')
                    .then((eventsRetrieved) => {

                        eventsRetrieved.forEach(event => {
                            let eventLocation = {
                                'lat': event.location.coordinates._lat,
                                'lng': event.location.coordinates._long
                            }

                            let eventNonFormattedLoc = event.location;
                            eventNonFormattedLoc['coordinates'] = eventLocation;
                            event['location'] = eventNonFormattedLoc;
                            context.commit('addEvent', event);
                            context.commit('addMapEventToShow', event.id);
                        });

                        resolve();
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ getEvents ~                                                               >
        // To get events that will be celebrated                                       >
        //----------------------------------------------------------------------------->
        getEventParticipants(context, eventID) {
            return new Promise(function (resolve, reject) {
                let event = context.getters.getEventByID(eventID);
                context.dispatch('database/getMultipleProfilesByUID', event.participants)
                    .then((userProfiles) => {
                        userProfiles.forEach(user => {
                            context.commit('users/addUser', user);
                        });
                        resolve(userProfiles);
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ resetState ~                                                              >
        // To reset state info                                                         >
        //----------------------------------------------------------------------------->
        resetState(context) {
            return new Promise(function (resolve) {
                context.commit('resetState');
                resolve();
            });
        },

        //----------------------------------------------------------------------------->
        // ~ addParticipantToEvent ~                                                   >
        // To add user to an Event                                                     >
        // parameters required: eventID                                                >
        //----------------------------------------------------------------------------->
        addParticipantToEvent: function (context, eventID) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/addParticipantToEvent', eventID)
                    .then((userID) => {
                        context.commit("addParticipantToEvent", {eventID: eventID, userID: userID});
                        resolve();
                    })
                    .catch((error) => {
                        console.log(error)
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ removeParticipantFromEvent ~                                              >
        // To remove user from an Event                                                >
        // parameters required: eventID                                                >
        //----------------------------------------------------------------------------->
        removeParticipantFromEvent: function (context, eventID) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/removeParticipantFromEvent', eventID)
                    .then((userID) => {
                        context.commit("removeParticipantFromEvent", {eventID: eventID, userID: userID});
                        resolve();
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ getEventByID ~                                                             >
        // To user profile info by its ID                                              >
        // parameters required: id                                                     >
        //----------------------------------------------------------------------------->
        getEventByID(context, id) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/getEventByID', id)
                    .then((event) => {
                        if (event) {
                            let userLocation = context.getters['user/getLocation'];
                            let distance = null;
                            let eventLocation = {
                                'lat': event.location.coordinates._lat,
                                'lng': event.location.coordinates._long
                            }

                            let eventNonFormattedLoc = event.location;
                            eventNonFormattedLoc['coordinates'] = eventLocation;
                            event['location'] = eventNonFormattedLoc;

                            if (userLocation != null) {
                                distance = haversine(event.location.coordinates, userLocation);
                            }

                            event = {
                                ...event,
                                'distance': distance / 1000
                            };
                            context.commit('addEvent', event);
                            resolve(event);
                        } else {
                            resolve(null);
                        }
                    })
                    .catch((error) => {
                        console.log("Error getting event by it's id in firebase.");
                        reject(error)
                    });
            });
        }

    }
}