import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import Constants from "../Constants";
import database from "./database";
import permissions from "./permissions";
import users from "./users.js";
import user from "./user.js";


////////////////////////////////////////////////////////
//-----               APP RANKING                -----//
////////////////////////////////////////////////////////

export default {
    namespaced: true,
    modules: {
        database,
        permissions,
        users,
        user
    },
    state: {
        lastUserShowed: -1,
        rankingUsers: [],
        rankingUsersIDs: [],
        rankingUser: null
},
    getters: {

        getRankingUsersToShow: (state, getters) => {
            let rus = [];

            let i = 0;
            state.rankingUsersIDs.forEach(uid => {
                let ru = {
                    ...getters['users/getUserByID'](uid)
                };

                ru["score"] = state.rankingUsers[i].score;
                ru["position"] = state.rankingUsers[i].position;

                rus.push(ru);
                i++;
            });

            return rus;
        },
        getLastUserShowed: (state) => {
            return state.lastUserShowed;
        },
        getRankedUserByIndex: (state) => (index) => {
            return state.rankingUsers[index];
        },
        getRankingUsers: (state) => {
            return state.rankingUsers;
        }
},
    mutations: {
        resetState(state) {
            state.lastUserShowed = -1;
            state.rankingUsers.splice(0, state.rankingUsers.length);
            state.rankingUsersIDs.splice(0, state.rankingUsersIDs.length);
            state.rankingUser = null;
        },
        setRankingUser(state, rankingUser) {
            state.rankingUser = rankingUser;
        },
        setRankingUsers(state, rankingUsers) {
            rankingUsers.forEach(r => {
                state.rankingUsers.push(r);
            });
        },
        resetRankingUsers(state) {
            state.rankingUsers.splice(0, state.rankingUsers.length);
        },
        resetRankingUsersIDs(state) {
            state.rankingUsersIDs.splice(0, state.rankingUsersIDs.length);
        },

        setLastUserShowed(state, lastUserShowed) {
            state.lastUserShowed = lastUserShowed;
        },
        addRankingUserID(state, rankingUserID) {
            if (-1 == state.rankingUsersIDs.findIndex(id => {
                return id == rankingUserID;
            })) {
                state.rankingUsersIDs.push(rankingUserID);
            }
        }
},
    actions: {
        //----------------------------------------------------------------------------->
        // ~ initState ~                                                               >
        // To init ranking state info with the appropriate data                        >
        //----------------------------------------------------------------------------->
        initState(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('resetState')
                    .then(() => {
                        context.dispatch('getRankingUsers')
                            .then(() => {
                                resolve();
                            })
                            .catch((error) => {
                                reject(error)
                            });
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });

        },

        //----------------------------------------------------------------------------->
        // ~ getRankingUsers ~                                                         >
        // To get top 100 ranking users and current user position in ranking           >
        //----------------------------------------------------------------------------->
        getRankingUsers(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/getTop100RankingUsers')
                    .then((top100Users) => {
                        let userID = context.getters['user/getID'];

                        let userIndex = top100Users.findIndex(user => {
                            return user.id == userID;
                        });

                        context.commit('setRankingUsers', top100Users);

                        if (userIndex == -1) {
                            context.dispatch('database/getUserRankingPositionByID', userID)
                                .then((user) => {
                                    context.commit('setRankingUser', user);
                                    resolve();
                                })
                                .catch((error) => {
                                    reject(error);
                                });
                        } else {
                            resolve();
                        }
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ getPagedRankingUsers ~                                                    >
        // To get page-size users from the group of ranking users retrieved            >
        //----------------------------------------------------------------------------->
        getPagedRankingUsers(context) {
            return new Promise(async function (resolve, reject) {
                let newUserShowed = context.getters.getLastUserShowed + 1;
                let pageEnding = newUserShowed + Constants.RANKING_PAGE_SIZE;
                if (newUserShowed == context.getters.getRankingUsers) {
                    resolve(false);
                }
                while (newUserShowed < pageEnding && newUserShowed < context.getters.getRankingUsers.length) {
                    let user = context.getters.getRankedUserByIndex(newUserShowed);

                    await context.dispatch('users/getUserByID', user.id)
                        .then(() => {
                            context.commit("addRankingUserID", user.id);
                        })
                        .catch((error) => {
                            reject(error);
                        });
                    newUserShowed++;
                }

                context.commit('setLastUserShowed', newUserShowed - 1);
                if (newUserShowed < context.getters.getRankingUsers.length) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            });
        },

        //----------------------------------------------------------------------------->
        // ~ areThereMoreRankingUsersToShow ~                                          >
        // To check if there are ranking users left to show                            >
        //----------------------------------------------------------------------------->
        areThereMoreRankingUsersToShow(context) {
            return new Promise(async function (resolve) {
                let newUserShowed = context.getters.getLastUserShowed + 1;
                if (newUserShowed < context.getters.getRankingUsers.length) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            });
        },

        //----------------------------------------------------------------------------->
        // ~ resetState ~                                                              >
        // To reset state info                                                         >
        //----------------------------------------------------------------------------->
        resetState(context) {
            return new Promise(function (resolve) {
                context.commit('resetState');
                resolve();
            });
        }
}
}