import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import database from "./database.js";
import users from "./users.js";
import i18n from "../../i18n";

let alreadyAccountUsernameError = i18n.t('messages.ERROR_ALREADY_AN_ACCOUNT_WITH_THIS_USERNAME');
let genericError = i18n.t('messages.ERROR_GENERIC_PROBLEM');
let gettingCurrentLocationError = i18n.t('messages.ERROR_GETTING_CURRENT_LOCATION');

////////////////////////////////////////////////////////
//-----         ACCOUNTS & PROFILES INFO         -----//
////////////////////////////////////////////////////////

export default {
    namespaced: true,
    modules: {
        database,
        users
    },
    state: {
        /* User account*/
        id: null,
        'name': null,
        lastName: null,
        email: null,
        chats: [],
        role: 'user',

        /* User profile */
        // 1st part
        username: null,
        birthDate: null,
        gender: null,
        description: null,
        avgRating: null,
        numRatings: null,
        // 2nd part
        knownLanguages: null,
        languagesToLearn: null,
        // 3rd part
        profileImg: null,

        /* Geolocation info */
        location: null,

        /* Ratings info */
        ratings: null,

        /* Constants */
        genderOptions: {
            "M": {
                img: "img/genders/male.svg",
                defaultProfileImg: 'img/defaultUserImages/defaultUserImage.svg'
            },
            "W": {
                img: "img/genders/female.svg",
                defaultProfileImg: 'img/defaultUserImages/defaultUserImage.svg'
            },
            "O": {
                img: "img/genders/other.svg",
                defaultProfileImg: 'img/defaultUserImages/defaultUserImage.svg'
            }
        },
        languageOptions: {
            "de": {
                img: "img/flags/de.svg"
            },
            "zh": {
                img: "img/flags/zh.svg"
            },
            "es": {
                img: "img/flags/es.svg"
            },
            "fr": {
                img: "img/flags/fr.svg"
            },
            "en": {
                img: "img/flags/en.svg"
            },
            "it": {
                img: "img/flags/it.svg"
            },
            "ja": {
                img: "img/flags/ja.svg"
            },
            "pt": {
                img: "img/flags/pt.svg"
            },
            "all": {
                img: "img/flags/all.svg"
            }
        }
    },
    getters: {
        getName: (state) => {
            return state.name;
        },
        getID: (state) => {
            return state.id;
        },
        getLastName: (state) => {
            return state.lastName;
        },
        getEmail: (state) => {
            return state.email;
        },
        getRole: (state) => {
            return state.role;
        },
        getChats: (state) => {
            return state.chats;
        },
        getProfile: (state) => {
            let data = {};

            if (state.username) {
                data['username'] = state.username;
            }
            if (state.birthDate) {
                data['birthDate'] = state.birthDate;
            }
            if (state.gender) {
                data['gender'] = state.gender;
            }
            if (state.description) {
                data['description'] = state.description;
            }
            if (state.profileImg) {
                data['profileImg'] = state.profileImg;
            }
            if (state.knownLanguages) {
                data['knownLanguages'] = state.knownLanguages;
            }
            if (state.languagesToLearn) {
                data['languagesToLearn'] = state.languagesToLearn;
            }

            data['avgRating'] = state.avgRating;
            data['numRatings'] = state.numRatings;

            return data;
        },
        getUsername: (state) => {
            return state.username;
        },
        getBirthDate: (state) => {
            return state.birthDate;
        },
        getGender: (state) => {
            return state.gender;
        },
        getDescription: (state) => {
            return state.description;
        },
        getProfileImg: (state) => {
            return state.profileImg;
        },
        getAvgRating: (state) => {
            return state.avgRating;
        },
        getNumRatings: (state) => {
            return state.numRatings;
        },
        getKnownLanguages: (state) => {
            return state.knownLanguages;
        },
        getLanguagesToLearn: (state) => {
            return state.languagesToLearn;
        },

        getGenderImgByID: (state) => (id) => {
            return state.genderOptions[id].img;
        },
        getGenderDefaultProfileImgByUserGender: (state) => {
            return state.genderOptions["O"].defaultProfileImg;
        },
        getGenderOptionsAsArray: (state, getters) => {
            let genderOptions = {
                "M": {
                    'text': i18n.t('messages.TEXT_MAN')
                },
                "W": {
                    'text': i18n.t('messages.TEXT_WOMAN')
                },
                "O": {
                    'text': i18n.t('messages.TEXT_OTHER')
                }
            };
            return Object.keys(genderOptions).map(function (id) {
                let text = genderOptions[id].text;
                let img = getters.getGenderImgByID(id);
                return new Object({id, text, img});
            });
        },
        getLanguageImgByID: (state) => (id) => {
            return state.languageOptions[id].img;
        },
        getLanguageOptionsAsArray: (state, getters) => {
            let languageOptions = {
                "de": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_DE')
                },
                "zh": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ZH')
                },
                "es": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ES')
                },
                "fr": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_FR')
                },
                "en": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_EN')
                },
                "it": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_IT')
                },
                "ja": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_JA')
                },
                "pt": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_PT')
                },
                "all": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ALL')
                }
            };
            return Object.keys(state.languageOptions).map(function (id) {
                let text = languageOptions[id].text;
                let img = getters.getLanguageImgByID(id);
                return new Object({id, text, img});
            });
        },
        getLanguageFromLanguageOptionsByID: (state) => (id) => {
            let languageOptions = {
                "de": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_DE')
                },
                "zh": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ZH')
                },
                "es": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ES')
                },
                "fr": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_FR')
                },
                "en": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_EN')
                },
                "it": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_IT')
                },
                "ja": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_JA')
                },
                "pt": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_PT')
                },
                "all": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ALL')
                }
            };

            return {
                ...state.languageOptions[id],
                'text': languageOptions[id].text
            };
        },
        getLocation: (state) => {
            return state.location;
        },
        getRatings: (state) => {
            return state.ratings;
        },
        getRating: (state) => (id) => {
            if (state.ratings) {
                return state.ratings[id];
            } else {
                return null;
            }
        }
    },
    mutations: {
        setIDe(state, id) {
            state.id = id;
        },
        setName(state, name) {
            state.name = name;
        },
        setLastName(state, lastName) {
            state.lastName = lastName;
        },
        setEmail(state, email) {
            state.email = email;
        },
        setRole(state, role) {
            state.role = role;
        },
        setChats(state, chats) {
            state.chats = chats;
        },
        setAccount(state, accountInfo) {
            if (accountInfo.hasOwnProperty('email')) {
                state.email = accountInfo.email;
            }
            if (accountInfo.hasOwnProperty('name')) {
                state.name = accountInfo.name;
            }
            if (accountInfo.hasOwnProperty('lastName')) {
                state.lastName = accountInfo.lastName;
            }
            if (accountInfo.hasOwnProperty('profileImg')) {
                state.role = accountInfo.role;
            }
            if (accountInfo.hasOwnProperty('id')) {
                state.id = accountInfo.id;
            }
            if (accountInfo.hasOwnProperty('chats')) {
                state.chats = accountInfo.chats;
            }
            if (accountInfo.hasOwnProperty('role')) {
                state.role = accountInfo.role;
            }
        },
        setUsername(state, username) {
            state.username = username;
        },
        setBirthDate(state, birthDate) {
            state.birthDate = birthDate;
        },
        setGender(state, gender) {
            state.gender = gender;
        },
        setDescription(state, description) {
            state.description = description;
        },
        setAvgRating(state, avgRating) {
            state.avgRating = avgRating;
        },
        setNumRatings(state, numRatings) {
            state.numRatings = numRatings;
        },
        setKnownLanguages(state, knownLanguages) {
            state.knownLanguages = knownLanguages;
        },
        setLanguagesToLearn(state, languagesToLearn) {
            state.languagesToLearn = languagesToLearn;
        },
        setProfileImg(state, profileImg) {
            state.profileImg = profileImg;
        },
        setRatings(state, ratings) {
            state.ratings = ratings;
        },
        setRating(state, data) {
            if (state.ratings == null) {
                state.ratings = {};
            }
            state.ratings[data.id] = data.rating;
        },

        setProfile(state, profileInfo) {
            if (profileInfo) {

                if (profileInfo.hasOwnProperty('username')) {
                    state.username = profileInfo.username;
                }
                if (profileInfo.hasOwnProperty('birthDate')) {
                    state.birthDate = profileInfo.birthDate;
                }
                if (profileInfo.hasOwnProperty('gender')) {
                    state.gender = profileInfo.gender;
                }
                if (profileInfo.hasOwnProperty('description')) {
                    state.description = profileInfo.description;
                }
                if (profileInfo.hasOwnProperty('avgRating')) {
                    state.avgRating = profileInfo.avgRating;
                }
                if (profileInfo.hasOwnProperty('numRatings')) {
                    state.numRatings = profileInfo.numRatings;
                }
                if (profileInfo.hasOwnProperty('knownLanguages')) {
                    state.knownLanguages = profileInfo.knownLanguages;
                }
                if (profileInfo.hasOwnProperty('languagesToLearn')) {
                    state.languagesToLearn = profileInfo.languagesToLearn;
                }
                if (profileInfo.hasOwnProperty('profileImg')) {
                    state.profileImg = profileInfo.profileImg;
                }
            }
        },
        resetState(state) {
            /* User account*/
            state.id = null;
            state.name = null;
            state.lastName = null;
            state.email = null;
            state.role = 'user';
            state.chats = [];

            /* User profile */
            // 1st part
            state.username = null;
            state.birthDate = null;
            state.gender = null;
            state.description = null;
            state.avgRating = null;
            state.numRatings = null;
            // 2nd part
            state.knownLanguages = null;
            state.languagesToLearn = null;
            // 3rd part
            state.profileImg = null;

            /* Ratings info */
            state.ratings = null;

            /* Geolocation info */
            state.location = null;
        },
        setLocation(state, location) {
            state.location = location;
        }
    },
    actions: {
        //----------------------------------------------------------------------------->
        // ~ initState ~                                                               >
        // To init users state info with the appropriate data                          >
        //----------------------------------------------------------------------------->
        initState(context) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/getProfile')
                    .then((profileInfo) => {
                        context.commit('setProfile', profileInfo);

                        context.dispatch('database/getAccount')
                            .then((accountInfo) => {
                                context.commit('setAccount', accountInfo);
                                context.dispatch('getRatings')
                                    .then(() => {
                                        resolve();
                                    })
                                    .catch((error) => {
                                        reject(error)
                                    });
                                resolve();
                            })
                            .catch((error) => {
                                reject(error)
                            });
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ resetState ~                                                              >
        // To reset state info                                                         >
        //----------------------------------------------------------------------------->
        resetState(context) {
            return new Promise(function (resolve) {
                context.commit('resetState');
                resolve();
            });
        },

        //----------------------------------------------------------------------------->
        // ~ createAccount ~                                                           >
        // To create user account                                                      >
        // parameters required: data {email, password, name, lastName}                 >
        //----------------------------------------------------------------------------->
        createAccount(context, data) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/createAccount', data)
                    .then(() => {
                        context.dispatch('database/setPersistance')
                            .then(() => {
                                context.dispatch('database/setAccount', data)
                                    .then(() => {
                                        context.commit('setAccount', data);
                                        resolve();
                                    })
                                    .catch((error) => {
                                        console.log('Error creating user account.');
                                        reject(error)
                                    });
                            })
                            .catch((error) => {
                                reject(error)
                            });
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ generateNotificationToken ~                                               >
        // To generate push notifications token for this browser and user              >
        //----------------------------------------------------------------------------->
        generateNotificationToken(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/generatePushNotificationToken')
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ updateAccountNameAndLastName ~                                            >
        // To update user account data name and last name                              >
        // parameters required: data {[name], [lastName]}                              >
        //----------------------------------------------------------------------------->
        updateAccountNameAndLastName(context, data) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/updateAccountData', data)
                    .then(() => {
                        if (data.hasOwnProperty('name')) {
                            context.commit('setName', data.name);
                        }
                        if (data.hasOwnProperty('lastName')) {
                            context.commit('setLastName', data.lastName);
                        }

                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ updateAccountEmail ~                                                      >
        // To update user account email                                                >
        // parameters required: email                                                  >
        //----------------------------------------------------------------------------->
        updateAccountEmail(context, email) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/updateAccountEmail', email)
                    .then(() => {
                        context.commit('setEmail', email);
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ updateAccountPassword ~                                                   >
        // To update user account password                                             >
        // parameters required: password                                               >
        //----------------------------------------------------------------------------->
        updateAccountPassword(context, password) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/updateAccountPassword', password)
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ login ~                                                                   >
        // To log user in                                                              >
        // parameters required: data {email, password}                                 >
        //----------------------------------------------------------------------------->
        login(context, data) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/login', data)
                    .then(() => {
                        context.commit('setAccount', data);
                        context.dispatch('database/setPersistance')
                            .then(() => {
                                resolve();
                            })
                            .catch((error) => {
                                reject(error)
                            });
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ isUserLogged ~                                                            >
        // To determine if user is or is not logged                                    >
        //----------------------------------------------------------------------------->
        isUserLogged: function (context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/isUserAuth')
                    .then((isIt) => {
                        resolve(isIt);
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ logout ~                                                                  >
        // To log user out                                                             >
        //----------------------------------------------------------------------------->
        logout(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/logout')
                    .then(() => {
                        context.dispatch('resetAppState', null, {root: true});
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ sendEmailVerification ~                                                   >
        // To send email to verify user email                                          >
        //----------------------------------------------------------------------------->
        sendEmailVerification(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/sendEmailVerification')
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error sending verification email.');
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ sendEmailVerification ~                                                   >
        // To send email to verify user email                                          >
        //----------------------------------------------------------------------------->
        sendPasswordResetEmail(context, email) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/sendPasswordResetEmail', email)
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error sending password email.');
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ checkIfEmailIsVerified ~                                                  >
        // To check if email is verified                                               >
        //----------------------------------------------------------------------------->
        checkIfEmailIsVerified(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/isEmailVerified')
                    .then((isVerified) => {
                        resolve(isVerified);
                    })
                    .catch((error) => {
                        console.log('Error checking if email is verified.');
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ checkIfUsernameIsAlreadyRegistered ~                                      >
        // To check if a username is already in use                                    >
        //----------------------------------------------------------------------------->
        checkIfUsernameIsAlreadyRegistered(context, username) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/getProfileByUsername', username)
                    .then((profileInfo) => {
                        if (profileInfo) {
                            resolve(true);
                        } else {
                            resolve(false);
                        }
                    })
                    .catch((error) => {
                        console.log('Error checking if username is already registered.');
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ createProfile ~                                                           >
        // To create user profile                                                      >
        // parameters required: data {username, birthDate, gender, description}        >
        //----------------------------------------------------------------------------->
        createProfile(context, data) {
            return new Promise(function (resolve, reject) {
                context.dispatch('checkIfUsernameIsAlreadyRegistered', data.username)
                    .then((isAlreadyRegistered) => {
                        if (isAlreadyRegistered) {
                            reject(alreadyAccountUsernameError);
                        } else {
                            context.dispatch('database/setProfile', data)
                                .then(() => {
                                    context.commit('setUsername', data.username);
                                    context.commit('setBirthDate', data.birthDate);
                                    context.commit('setGender', data.gender);
                                    context.commit('setDescription', data.description);
                                    context.commit('setAvgRating', 0);
                                    context.commit('setNumRatings', 0);
                                    resolve();
                                })
                                .catch((error) => {
                                    console.log('Error creating user profile.');
                                    reject(error);
                                });
                        }
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ updateProfile ~                                                           >
        // To update user profile data                                                 >
        // parameters required: data {[username], [birthDate], [gender],               >
        //                            [description], [knownLanguages],                 >
        //                            [languagesToLearn]}                              >
        //----------------------------------------------------------------------------->
        updateProfile(context, data) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/updateProfile', data)
                    .then(async () => {

                        if (data.hasOwnProperty('username')) {
                            context.commit('setUsername', data.username);
                        }
                        if (data.hasOwnProperty('description')) {
                            context.commit('setDescription', data.description);
                        }
                        if (data.hasOwnProperty('gender')) {
                            context.commit('setGender', data.gender);
                        }
                        if (data.hasOwnProperty('birthDate')) {
                            context.commit('setBirthDate', data.birthDate);
                        }
                        if (data.hasOwnProperty('knownLanguages')) {
                            context.commit('setKnownLanguages', data.knownLanguages);
                        }
                        if (data.hasOwnProperty('languagesToLearn')) {
                            context.commit('setLanguagesToLearn', data.languagesToLearn);
                        }
                        if (data.hasOwnProperty('profileImg')) {
                            await context.dispatch('setProfileImg', data.profileImg).catch((error) => {
                                reject(error);
                            });
                        }

                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ setLanguages ~                                                            >
        // To set user languages: known and to-be-learned                              >
        // parameters required: data {knownLanguages, languagesToLearn}                >
        //----------------------------------------------------------------------------->
        setLanguages(context, data) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/setLanguages', data)
                    .then(() => {
                        context.commit('setKnownLanguages', data.knownLanguages);
                        context.commit('setLanguagesToLearn', data.languagesToLearn);
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error setting user languages.');
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ setProfileImg ~                                                           >
        // To set user profile image                                                   >
        // parameters required: img                                                    >
        //----------------------------------------------------------------------------->
        setProfileImg(context, img) {
            return new Promise(function (resolve, reject) {
                Vue.nextTick(() => {

                    context.dispatch('database/uploadProfileImg', img)
                        .then((url) => {
                            context.dispatch('database/setProfileImg', url)
                                .then(() => {
                                    context.commit('setProfileImg', url);
                                    resolve();
                                })
                                .catch((error) => {
                                    console.log('Error setting user profile image.');
                                    console.error(error)
                                    reject(error);
                                });
                        })
                        .catch((error) => {
                            console.log('Error uploading user profile image.');
                            reject(error);
                        });
                });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ setRegistrationCompleted ~                                                >
        // To set user registration state to completed                                 >
        //----------------------------------------------------------------------------->
        setRegistrationCompleted(context, img) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/setRegistrationCompleted', img)
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error updating user registration state.');
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ getRegistrationState ~                                                    >
        // To check what register info the user has already submitted                  >
        //----------------------------------------------------------------------------->
        getRegistrationState(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/getProfile')
                    .then((profileInfo) => {

                        context.dispatch('checkRegistrationState', profileInfo)
                            .then((state) => {
                                context.commit('setProfile', profileInfo);
                                resolve(state);
                            })
                            .catch((error) => {
                                console.log(error);
                                reject(error);
                            });
                    })
                    .catch((error) => {
                        console.error(error);
                        reject(genericError);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ checkRegistrationState ~                                                  >
        // To check registration state of a profile                                    >
        // parameters: data {username, gender, description, avgRating, numRatings,     >
        //                   knownLanguages, languagesToLearn, profileImg}             >
        //----------------------------------------------------------------------------->
        checkRegistrationState(context, data) {

            return new Promise(function (resolve, reject) {

                context.dispatch('checkIfEmailIsVerified')
                    .then((isVerified) => {
                        if (isVerified) {
                            // Part 0 completed
                            if (data && data.hasOwnProperty('username') && data.hasOwnProperty('gender')
                                && data.hasOwnProperty('description') && data.hasOwnProperty('avgRating')
                                && data.hasOwnProperty('numRatings')) {
                                // Part 1 completed
                                if (data.hasOwnProperty('knownLanguages') && data.hasOwnProperty('languagesToLearn')) {
                                    // Part 2 completed
                                    if (data.hasOwnProperty('profileImg')) {
                                        // Part 3 completed
                                        // Registration process completed
                                        resolve('dashboard');
                                    } else {
                                        // State 3
                                        resolve('set-your-profile-image');
                                    }
                                } else {
                                    // State 2
                                    resolve('fill-your-languages');
                                }
                            } else {
                                // State 1
                                resolve('fill-your-profile-info');
                            }
                        } else {
                            // State 0
                            resolve('email-verification');
                        }
                    })
                    .catch((error) => {
                        console.error(error);
                        reject(genericError);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ getCurrentLocation ~                                                      >
        // To get the current user location                                            >
        //----------------------------------------------------------------------------->
        getCurrentLocation() {
            return new Promise(function (resolve, reject) {

                navigator.geolocation.getCurrentPosition(position => {
                        resolve({
                            lat: position.coords.latitude,
                            lng: position.coords.longitude
                        })
                    },
                    function (error) {
                        console.error(error);
                        reject(gettingCurrentLocationError);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ getRatings ~                                                              >
        // To get user ratings given by the logged user                                >
        //----------------------------------------------------------------------------->
        getRatings(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/getRatings')
                    .then((ratingsInfo) => {
                        context.commit('setRatings', ratingsInfo);
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error retrieving user ratings.');
                        reject(error);
                    });
            });
        },

        //----------------------------------------------------------------------------->
        // ~ rateUser ~                                                                >
        // To rate a user                                                              >
        // parameters: data{id, rating}                                                >
        //----------------------------------------------------------------------------->
        rateUser(context, data) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/setRating', data)
                    .then(() => {
                        context.commit('setRating', data);
                        context.commit('users/addRating', data);

                        resolve();
                    })
                    .catch((error) => {
                        console.log(error)
                        console.log('Error rating user.');
                        reject(error);
                    });
            });
        },


        //----------------------------------------------------------------------------->
        // ~ getLocation ~                                                             >
        // To get the current (or last) user location                                  >
        //----------------------------------------------------------------------------->
        getLocation(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('getCurrentLocation')
                    .then((currentLocation) => {
                        context.commit('setLocation', currentLocation);
                        context.dispatch('database/updateUserLocation', currentLocation)
                            .then(() => {
                                resolve(currentLocation);
                            })
                            .catch(() => {
                                resolve(currentLocation);
                            });
                    })
                    .catch((error) => {
                        console.error(error);
                        context.dispatch('database/getLocation')
                            .then((location) => {
                                context.commit('setLocation', location);
                                resolve(location);
                            })
                            .catch((error) => {
                                reject(error);
                            });
                    });
            });
        }
    }
};